const state = {
    showModal: false,
    content: '',
};
const getters = {
    getConfirmModal: (state) => state.showModal,
    getContent: (state) => state.content,
};
const mutations = {
    setModalValue(stata, data) {
        stata.showModal = data.showModal;
        stata.content = data.content;
    }
    //
};
const actions = {
    confirmPopUp({ commit }, data) {
        commit("setModalValue", data);
    },
};
const store = {
    namespaced: true,
    state,
    getters,
    mutations,
    actions,
};
export default store;
