/* eslint-disable @typescript-eslint/explicit-module-boundary-types, @typescript-eslint/no-explicit-any, @typescript-eslint/no-unused-vars */
import VueStorage from "./storage";
import VueWNIInterface, { WNInterface } from "vue-wni";
const plugin = {
    install: (app, options) => {
        app.use(VueStorage);
        app.use(VueWNIInterface);
        app.provide("$wni", WNInterface);
        app.config.globalProperties.$wni = WNInterface;
    },
};
export default plugin;
