import { createWebHistory, createRouter } from "vue-router";
const URLPATH = "/pushapp/appMain/fcm";
const routes = [
    //스플래시
    {
        path: `${URLPATH}` + '/splash',
        name: "Splash",
        component: () => import("./views/Splash/Splash.vue"),
    },
    //로그인
    {
        path: `${URLPATH}` + `/login`,
        name: "Login",
        component: () => import("./views/Login/Login.vue"),
    },
    //메인 홈
    {
        path: `${URLPATH}` + `/home`,
        name: "Home",
        component: () => import("./views/Home/Home.vue"),
    },
    //사이드 메뉴
    {
        path: `${URLPATH}` + `/sidemenu`,
        name: "SideMenu",
        component: () => import("./views/SideMenu/SideMenu.vue"),
    },
    //알람 메뉴
    {
        path: `${URLPATH}` + `/alarm`,
        name: "Alarm",
        component: () => import("./views/Alarm/Alarm.vue"),
    },
    //숨긴 리스트
    {
        path: `${URLPATH}` + `/hiddenlist`,
        name: "HiddenList",
        component: () => import("./views/HiddenList/HiddenList.vue"),
    },
    //팝업1 확인 팝업
    {
        path: `${URLPATH}` + `/confirmpopup`,
        name: "ConfirmPopUp",
        props: true,
        component: () => import("./views/PopUp/ConfirmPopUp.vue"),
    },
    //팝업2 취소/확인 팝업
    {
        path: `${URLPATH}` + `/selectpopup`,
        name: "SelectPopUp",
        component: () => import("./views/PopUp/SelectPopUp.vue"),
    },
    //팝업3 업데이트 팝업
    {
        path: `${URLPATH}` + `/updatepopup`,
        name: "UpdatePopUp",
        props: true,
        component: () => import("./views/PopUp/UpdatePopUp.vue"),
    },
    //메인 홈
    {
        path: `${URLPATH}` + `/helloworld`,
        name: "HelloWorld",
        component: () => import("./components/HelloWorld.vue"),
    },
    //메인 홈
    {
        path: `${URLPATH}` + `/pushTest`,
        name: "PushTest",
        component: () => import("./views/Test/PushTest.vue"),
    },
    {
        path: '/:pathMatch(.*)*',
        name: 'Error',
        component: import("./views/Error/Error.vue")
    }
];
const router = createRouter({
    history: createWebHistory(),
    routes: routes,
    scrollBehavior(to, from, savedPosition) {
        return { left: 0, top: 0 };
    }
});
export default router;
