const state = {
    showModal: false,
    logoutValue: '0',
    deleteValue: '0',
    restoreValue: '0',
    value: '',
};
const getters = {
    getSelectModal: (state) => state.showModal,
    getSeLogoutValue: (state) => state.logoutValue,
    getRestoreValue: (state) => state.restoreValue,
    getDeleteValue: (state) => state.deleteValue,
    getPopupValue: (state) => state.value
};
const mutations = {
    setModalValue(stata, data) {
        stata.showModal = data.showModal;
        stata.logoutValue = data.logoutValue;
        stata.deleteValue = data.deleteValue;
        stata.restoreValue = data.restoreValue;
        state.value = data.value;
    }
    //
};
const actions = {
    selectPopUp({ commit }, data) {
        commit("setModalValue", data);
    },
};
const store = {
    namespaced: true,
    state,
    getters,
    mutations,
    actions,
};
export default store;
