import Network from "../../network";
const state = {
    id: '',
    pw: '',
};
const getters = {
//
};
const mutations = {
//
};
const actions = {
    async login({ commit }, data) {
        const login_value = await new Network().ServerConnect('/pushapp/appMain/appUserLogin', data, "POST");
        return login_value;
    },
};
const store = {
    namespaced: true,
    state,
    getters,
    mutations,
    actions,
};
export default store;
