import Network from "../../network";
const state = {
//
};
const getters = {
//
};
const mutations = {
//
};
const actions = {
    async pushList({ commit }, data) {
        const PUSHLIST = await new Network().ServerConnectAddToken('/pushapp/appMain/checkPushList', data, "POST");
        if (PUSHLIST !== 'error') {
            return PUSHLIST;
        }
        else {
            return 'error';
        }
    },
    //푸시 영구 삭제
    async pushDelete({ commit }, data) {
        const PUSHDELETE = await new Network().ServerConnectAddToken('/pushapp/appMain/changeMessageDelYn ', data, "POST");
        return PUSHDELETE;
    },
    //푸시 복원
    async pushRestore({ commit }, data) {
        const PUSHRESTORE = await new Network().ServerConnectAddToken('/pushapp/appMain/changeMessageViewYn', data, "POST");
        return PUSHRESTORE;
    },
    //푸시 숨기기
    async pushHidden({ commit }, data) {
        const PUSHRESTORE = await new Network().ServerConnectAddToken('/pushapp/appMain/changeMessageViewYn', data, "POST");
        return PUSHRESTORE;
    },
};
const store = {
    namespaced: true,
    state,
    getters,
    mutations,
    actions,
};
export default store;
