import { WNInterface } from "vue-wni";
export class Storage {
    constructor() {
    }
    getItem(key) {
        return new Promise(async (resolve, reject) => {
            try {
                if (WNInterface.isNative) {
                    await WNInterface.execute("wnGetStorage", { key: key, callback: WNInterface.cb((data) => {
                            const value = data.value;
                            resolve(value);
                        }) });
                }
                else {
                    const value = window.localStorage.getItem(key);
                    resolve(value);
                }
            }
            catch (e) {
                reject(e);
            }
        });
    }
    setItem(key, value) {
        return new Promise(async (resolve, reject) => {
            try {
                if (WNInterface.isNative) {
                    await WNInterface.execute("wnSetStorage", { key: key, value: value, callback: WNInterface.cb((data) => {
                            resolve(true);
                        }) });
                }
                else {
                    window.localStorage.setItem(key, value);
                    resolve(true);
                }
            }
            catch (e) {
                reject(e);
            }
        });
    }
    removeItem(key) {
        return new Promise(async (resolve, reject) => {
            try {
                if (WNInterface.isNative) {
                    await WNInterface.execute("wnSetStorage", { key: key, value: "" });
                }
                else {
                    window.localStorage.removeItem(key);
                }
                resolve(true);
            }
            catch (e) {
                reject(e);
            }
        });
    }
    static getItem(key) {
        return this._default.getItem(key);
    }
    static removeItem(key) {
        return this._default.removeItem(key);
    }
    static setItem(key, value) {
        return this._default.setItem(key, value);
    }
    static getInstance() {
        if (!Storage._default) {
            Storage._default = new Storage();
        }
        return Storage._default;
    }
}
const plugin = {
    install: (app, options = {}) => {
        const storage = Storage.getInstance();
        app.provide("$storage", storage);
        app.config.globalProperties.$storage = storage;
    },
};
export default plugin;
