import { defineComponent } from 'vue';
import $ from "jquery";
export default defineComponent({
    name: 'App',
    components: {
    //
    },
    data() {
        return {
            transitionName: "",
        };
    },
    mounted() {
        document.querySelector('html').style.overflow = 'hidden';
        document.querySelector('html').style.width = '100%';
        document.querySelector('html').style.height = '100%';
        document.querySelector('body').style.overflow = 'auto';
        document.querySelector('body').style.width = '100%';
        document.querySelector('body').style.height = '100%';
    },
    watch: {
        $route(to, from) {
            $("main").scrollTop(0);
            if (from.name === "Home" && to.name === "HelloWorld") {
                this.transitionName = "next";
            }
            else if (from.name === "HelloWorld" && to.name === "Home") {
                this.transitionName = "prev";
            }
            else if (from.name === "Splash" && to.name === "Login") {
                this.transitionName = "next";
            }
            else if (from.name === "Login" && to.name === "Home") {
                this.transitionName = "next";
            }
            else if (from.name === "Splash" && to.name === "Home") {
                this.transitionName = "next";
            }
            else if (from.name === "Home" && to.name === "Splash") {
                this.transitionName = "next";
            }
            else if (from.name === "Home" && to.name === "HiddenList") {
                this.transitionName = "next";
            }
            else if (from.name === "HiddenList" && to.name === "Home") {
                this.transitionName = "prev";
            }
            else if (from.name === "Home" && to.name === "Alarm") {
                this.transitionName = "next";
            }
            else if (from.name === "Alarm" && to.name === "Home") {
                this.transitionName = "prev";
            }
        }
    }
});
