import axios from "axios";
import { Storage } from "@/plugins/storage";
import router from '../router';
//주소 값 설정해주기
axios.defaults.baseURL = `${process.env.VUE_APP_URL}`;
export default class Network {
    async ServerConnect(url, params = {}, method) {
        let data;
        let status;
        try {
            const res = await axios({
                method: method,
                url: url,
                headers: {
                    "Content-Type": "application/json",
                },
                data: params,
            });
            status = res.status;
            //정상 연결일 때만 데이터 전달
            if (status === 200) {
                if (res.data.status === 200) {
                    data = res.data;
                    if (data.datas[0]['accessToken']) {
                        await Storage.setItem("accessToken", data.datas[0]['accessToken']);
                    }
                }
                else if (res.data.status === 406) {
                    data = "loginFail";
                }
                else if (res.data.status === 404 || res.data.status === 514) {
                    data = "tokenExpire";
                }
                else {
                    data = "error";
                }
            }
            else {
                data = "error";
            }
        }
        catch (e) {
            if (e.response.status === 500) {
                data = "error";
            }
            else if (e.response.status === 406) {
                data = "loginFail";
            }
            else if (e.response.status === 404 || e.response.status === 514) {
                data = "tokenExpire";
            }
            else if (e.response.status == 405 || e.response.status === 401) {
                await router.push({ name: 'Login' });
            }
            else {
                data = "error";
            }
        }
        return data;
    }
    async ServerConnectAddRefreshToken(url, params = {}, method) {
        let data;
        let status;
        const token = await Storage.getItem("refreshToken");
        try {
            const res = await axios({
                method: method,
                url: url,
                headers: {
                    "Content-Type": "application/json",
                    'Authorization': token,
                },
                data: params,
            });
            status = res.status;
            if (status === 200) {
                if (res.data.status == 200) {
                    data = res.data;
                    if (data.datas[0]['accessToken']) {
                        await Storage.setItem("accessToken", data.datas[0]['accessToken']);
                        await Storage.setItem("refreshToken", data.datas[0]['refreshToken']);
                    }
                }
                else if (res.data.status == 404 || res.data.status == 514) {
                    data = "tokenExpire";
                }
                else if (res.data.status == 405 || res.data.status == 401) {
                    await router.push({ name: 'Login' });
                }
                else {
                    data = "error";
                }
            }
            else {
                data = "error";
            }
        }
        catch (e) {
            if (e.response.status === 500) {
                data = "error";
            }
            else if (e.response.status === 404 || e.response.status == 514) {
                data = "tokenExpire";
            }
            else if (e.response.status == 405 || e.response.status === 401) {
                await router.push({ name: 'Login' });
            }
            else {
                data = "error";
            }
        }
        return data;
    }
    async ServerConnectAddToken(url, params = {}, method) {
        let data;
        let status;
        const token = await Storage.getItem("accessToken");
        try {
            const res = await axios({
                method: method,
                url: url,
                headers: {
                    "Content-Type": "application/json",
                    'Authorization': token,
                },
                data: params,
            });
            status = res.status;
            if (status === 200) {
                if (res.data.status === 200) {
                    data = res.data;
                    if (data.datas[0]['accessToken']) {
                        await Storage.setItem("accessToken", data.datas[0]['accessToken']);
                    }
                }
                else if (res.data.status == 404 || res.data.status == 514) {
                    data = "tokenExpire";
                }
                else if (res.data.status == 405 || res.data.status == 401) {
                    await router.push({ name: 'Login' });
                }
                else {
                    data = "error";
                }
            }
            else {
                data = "error";
            }
            return data;
        }
        catch (e) {
            if (e.response.status == 500) {
                data = "error";
            }
            else if (e.response.status === 404 || e.response.status === 514) {
                data = "tokenExpire";
            }
            else if (e.response.status == 405 || e.response.status === 401) {
                await router.push({ name: 'Login' });
            }
            else {
                data = "error";
            }
            return data;
        }
    }
}
