import Network from "../../network";
const state = {
    id: '',
    pw: '',
};
const getters = {
//
};
const mutations = {
//
};
const actions = {
    async logout({ commit }, data) {
        const Logout_value = await new Network().ServerConnectAddToken('/pushapp/appMain/requestLogOut', data, "POST");
        return Logout_value;
    },
};
const store = {
    namespaced: true,
    state,
    getters,
    mutations,
    actions,
};
export default store;
