import Network from "../../network";
const state = {
    userId: '',
    userName: '',
    marketingPushValue: 'Y',
    nightPushValue: 'Y',
};
const getters = {
    getId: (state) => state.userId,
    getUserName: (state) => state.userName,
    getMarketingPushValue: (state) => state.marketingPushValue,
    getNightPushValue: (state) => state.nightPushValue
};
const mutations = {
    setMarketingPush(stata, data) {
        stata.marketingPushValue = data.pushNormalAcceptYn;
    },
    setNightPush(stata, data) {
        stata.nightPushValue = data.changePushNightAcceptYn;
    },
    setUserInfo(stata, data) {
        stata.userId = data.userId;
        stata.userName = data.userNm;
        stata.marketingPushValue = data.pushNormalAcceptYn;
        stata.nightPushValue = data.pushNightAcceptYn;
    },
    setRemoveUserInfo(stata) {
        stata.userId = '';
        stata.userName = '';
        stata.marketingPushValue = 'Y';
        stata.nightPushValue = 'Y';
    }
};
const actions = {
    //유저 정보 가져오기
    async getUserInfo({ commit }, data) {
        const USERINFOVALUE = await new Network().ServerConnectAddToken('/pushapp/appMain/appUserInfo', data, "POST");
        if (USERINFOVALUE !== 'error' && USERINFOVALUE !== "tokenExpire" && USERINFOVALUE !== "notToken") {
            commit("setUserInfo", USERINFOVALUE.datas[0]);
            return USERINFOVALUE;
        }
        else if (USERINFOVALUE === "tokenExpire") {
            return "tokenExpire";
        }
        else if (USERINFOVALUE === "notToken") {
            return "notToken";
        }
        else {
            return 'error';
        }
    },
    //유저 정보 가져오기
    async removeUserinfo({ commit }) {
        commit("setRemoveUserInfo");
    },
    //마케팅 푸시 수신 동의
    async marketingPushUpdate({ commit }, data) {
        const PUSHUPDATE = await new Network().ServerConnectAddToken('/pushapp/appMain/changePushNormalAcceptYn', data, "POST");
        if (PUSHUPDATE !== 'error') {
            commit("setMarketingPush", data);
            return PUSHUPDATE;
        }
        else {
            return 'error';
        }
    },
    //야간 알림 푸시 알림 동의
    async nightPushUpdate({ commit }, data) {
        const NIGHTPUSHUPDATE = await new Network().ServerConnectAddToken('/pushapp/appMain/changePushNightAcceptYn', data, "POST");
        if (NIGHTPUSHUPDATE !== 'error') {
            commit("setNightPush", data);
            return NIGHTPUSHUPDATE;
        }
        else {
            return 'error';
        }
    }
};
const store = {
    namespaced: true,
    state,
    getters,
    mutations,
    actions,
};
export default store;
